<template>
    <el-form :model="replyForm" :rules="replyFormRules" ref="replyFormRef">
        <el-form-item prop="content" style="margin-bottom: 2px">
            <el-input v-model="replyForm.content" type="textarea" placeholder=""
                      autosize></el-input>
        </el-form-item>
        <el-form-item style="text-align: right; margin-bottom: 2px">
            <el-button size="small" @click="quit"></el-button>
            <el-button size="small" type="primary" @click="reply()"> </el-button>
        </el-form-item>
    </el-form>
</template>

<script>
export default {
    props:['id','blogId'],
    data(){
        return {
            replyForm: {
                content: ''
            },
            replyFormRules: {
                content: [
                    {required: true, message: ""},
                    {min: 0, max: 100, message: "100"}
                ]
            },
        }
    },
    methods:{
        // 
        reply() {
            this.$refs.replyFormRef.validate( async valid => {
                if (!valid) return
                let comment = {}
                comment.content = this.replyForm.content
                comment.blogId = this.blogId
                this.userInfo = JSON.parse(window.sessionStorage.getItem('user'))
                if (this.userInfo === null) {
                    this.$message({message: "", type: 'error', offset: 80});
                } else {
                    const {data: res} = await this.$blog.post('comments', {
                        content: comment.content,
                        blogId: comment.blogId,
                        userId: this.userInfo.id,
                        parentId: this.id
                    })
                    if (res.code === 200) {
                        this.$emit('newCmt',res.data)
                    } else {
                        this.$message({message: "", type: 'error', offset: 80});
                    }
                }
            })
        },
        quit(){
            this.$emit('quit','-1')
        }
    }
}
</script>

